@import "./../../abstracts/functions";
@import "./../../abstracts/variables";
@import "./../../abstracts/mixins/breakpoints-mixin";
@import "./../../base/root";

.acf-editor-wrap iframe {
	min-height: 0;
}

#postdivrich:target {
	outline: 2px dashed;
	outline-offset: 2px;
}

table.media .column-title .media-icon img {
	max-height: 60px;
}

.color-preview {
	--color: transparent;
	width: 1rem;
	height: 1rem;
	display: inline-block;
	box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.1);
	border-radius: 3px;
	background: var(--color);
	vertical-align: middle;
}

.mce-menu-item.mce-menu-item-preview.mce-active {
	border-left: none !important;
	.mce-text {
		color: white !important;
		&:before {
			content: "âœ“ ";
		}
	}
}

.acf-flexible-content .layout:has([data-name="hide_block"] .acf-switch.-on) {
	.acf-fc-layout-handle:after {
		content: " \2014  Hidden";
		opacity: 0.625;
	}
}

.mce-menu-item .mce-text {
	all: unset !important;
}

.core-column {
	--_width: 200px;
	@include bp(xl) {
		float: left !important;
		clear: none !important;
		&--content {
			width: calc(100% - var(--_width)) !important;
		}

		&--width {
			padding-left: 0px !important;
			width: var(--_width) !important;
		}
	}
}

.core-hide-label .acf-label {
	display: none;
}
